/*–§—É–Ω–∫—Ü–∏—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è —Ä–∞–∑–º–µ—Ç–∫–∏ –≤ —É–∫–∞–∑–∞–Ω–Ω—ã–π –µ–ª–µ–º–µ–Ω—Ç*/
export const appendMarkup = (elem,data) => {
    elem.insertAdjacentHTML('beforeend', data);
};

/*–§—É–Ω–∫—Ü–∏—è –æ—á–∏—Å—Ç–∫–∏ —Ä–∞–∑–º–µ—Ç–∫–∏ –≤ —É–∫–∞–∑–∞–Ω–Ω–æ–º –µ–ª–µ–º–µ–Ω—Ç–µ*/
export const clearMarkup = elem => {
    elem.innerHTML = '';
};