import { Block, Notify } from 'notiflix';

Block.init({
  svgSize: '80px',
  svgColor: '#2835e3',
  fontFamily: 'Roboto',
  useGoogleFont: true,
  backgroundColor: 'transparent',
});

Notify.init({
  width: '240px',
  position: 'right-bottom',
  timeout: 3000,
  distance: '10px',
  opacity: 1,
  borderRadius: '5px',
  rtl: false,
  messageMaxLength: 110,
  backOverlay: false,
  backOverlayColor: 'rgba(0,0,0,0.5)',
  plainText: true,
  showOnlyTheLastOne: false,
  clickToClose: false,
  pauseOnHover: true,
  ID: 'NotiflixNotify',
  className: 'notiflix-notify',
  zindex: 4001,
  useGoogleFont: false,
  fontFamily: 'Quicksand',
  fontSize: '13px',
  cssAnimation: true,
  cssAnimationDuration: 350,
  cssAnimationStyle: 'fade',
  closeButton: false,
  useIcon: true,
  useFontAwesome: false,
  fontAwesomeIconStyle: 'basic',
  fontAwesomeIconSize: '34px',
});

/*–§—É–Ω–∫—Ü–∏—è –ø–æ–∫–∞–∑–∞ "—É—Å–ø–µ—à–Ω–æ–≥–æ" —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è */
export const showSuccesMessage = message => {
  Notify.success(message);
};

/*–§—É–Ω–∫—Ü–∏—è –ø–æ–∫–∞–∑–∞ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ  */
export const showFailureMessage = message => {
  Notify.failure(message);
};

/*–§—É–Ω–∫—Ü–∏—è –ø–æ–∫–∞–∑–∞ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è —Å –ø—Ä–µ–¥—É–ø—Ä–µ–∂–¥–µ–Ω–∏–µ–º  */
export const showWarningMessage = message => {
  Notify.warning(message);
};

/*–§—É–Ω–∫—Ü–∏—è –∑–∞–ø—É—Å–∫–∞ loader-–∞  –Ω–∞ —É–∫–∞–∑–∞–Ω–æ–º –µ–ª–µ–º–µ–Ω—Ç–µ */
export const enableLoader = (elemClassName, text) => {
  Block.standard(elemClassName, text);
};

/*–§—É–Ω–∫—Ü–∏—è –ø—Ä–µ–∫—Ä–∞—â–µ–Ω–∏—è –ø–æ–∫–∞–∑–∞ loader-–∞  */
export const disableLoader = elemClassName => {
  Block.remove(elemClassName);
};
