/*
 * –§—É–Ω–∫—Ü–∏—è —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∞—Ü–∏–∏ –∂–∞–Ω—Ä–æ–≤ —Ñ–∏–ª—å–º–æ–≤ –≤ —á–∏—Ç–∞–±–µ–ª—å–Ω—ã–π –≤–∞—Ä–∏–∞–Ω—Ç. –í —Ñ—É–Ω–∫—Ü–∏—é –ø–µ—Ä–µ–¥–∞–µ—Ç—å—Å—è 2 –æ—Å–Ω–æ–≤–Ω—ã—Ö –∞—Ä–≥—É–º–µ–Ω—Ç–∞ -
 * –æ–±—ä–µ–∫—Ç —Ñ–∏–ª—å–º–æ–≤ –¥–ª—è —Ä–µ–Ω–¥–µ—Ä–∞ –∏ –æ–±—ä–µ–∫—Ç —Å –∂–∞–Ω—Ä–∞–º–∏. –î–ª—è –≤–∞—Ä–∏–∞–Ω—Ç–∞ —Å –º–æ–¥–∞–ª–∫–æ–π - –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–µ—Ä–µ–¥–∞—Ç—å –µ—â–µ 3 –Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π
 * –∞—Ä–≥—É–º–µ–Ω—Ç "all" –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –±–æ–ª—å—à–µ 2 –∂–∞–Ω—Ä–æ–≤.
 */

const genresTransformation = (dataObj, genresObj, genresLimit = 2) => {
  const data = { ...dataObj };
  const isUnlimit = genresLimit === 'all' ? true : false;

  const genresChanging = (arr, { genres } = genresObj) => {
    const outputArr = [];
    for (let i = 0; i < arr.length; i += 1) {
      if (i > genresLimit - 1) {
        outputArr.push('Other');
        break;
      }
      const currentGenre = genres.find(elem => elem.id === arr[i]);

      if (currentGenre) {
        outputArr.push(currentGenre.name);
      } else {
        outputArr.push('some');
      }
    }
    return outputArr;
  };

  if (!isUnlimit) {
    data.results.map(elem => {
      elem.genre_ids = genresChanging(elem.genre_ids, genresObj).join(', ');
    });
  } else {
    data.genres = [...data.genres].map(elem => elem.name).join(', ');
  }

  return data;
};

export default genresTransformation;
