/*–∫–ª–∞—Å –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –±–∏–±–ª–∏–æ—Ç–µ–∫–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ localStorage */
export default class LibraryApi {
    constructor() {
        this.page = 1;
        this.isEndStatus = false;
        this.activeLibrary = null;
    }



    /*–º–µ—Ç–æ–¥ –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö –∏–∑ —É–∫–∞–∑–∞–Ω–Ω–æ–π –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ –≤ localStorage*/
    getData(libraryType) {
        const storageData = localStorage.getItem(libraryType);

        if (storageData) {
            try {
                return JSON.parse(storageData);
            } catch (error) {
                console.error("Get state error: ", error.message);
            }
        }

        return null;
    }



    /*–º–µ—Ç–æ–¥ –¥–ª—è –∑–∞–ø–∏—Å–∏ –µ–ª–µ–º–µ–Ω—Ç–∞ –≤ —É–∫–∞–∑–∞–Ω–Ω—É—é –±–∏–±–ª–∏–æ—Ç–µ–∫—É –≤ localStorage*/
    setData(data, libraryType) {
        const STORAGE_DATA_TMP = {
            library: [],
            total_pages: 1,
            total_results:1
        };

        let storageData = this.getData(libraryType);

        if (storageData) {
            storageData.library.push(data);
            storageData.total_results += 1;
            storageData.total_pages = Math.ceil(storageData.total_results / 20);
        } else {
            storageData = { ...STORAGE_DATA_TMP };
            storageData.library.push(data);
        }

        localStorage.setItem(libraryType,JSON.stringify(storageData));
    }



    /*–º–µ—Ç–æ–¥ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –µ–ª–µ–º–µ–Ω—Ç–∞ –∏–∑ —É–∫–∞–∑–∞–Ω–Ω–æ–π –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ –≤ localStorage*/
    removeData(filmId, libraryType) {
        const storageData = this.getData(libraryType);

        if (!storageData || storageData.total_results===0) {
            alert("The library is Empty!");
            return;
        }
        
        const isFound=storageData.library.find((el, idx) => {
            if (el.id !== filmId) return false;
            
            storageData.library.splice(idx, 1);
            storageData.total_results -= 1;
            storageData.total_pages = Math.ceil(storageData.total_results / 20);
            return true;
        });

        if (!isFound) {
            alert('There is no such film in the library!');
            return;
        }
        localStorage.setItem(libraryType,JSON.stringify(storageData));
    }



    /*
     * –ú–µ—Ç–æ–¥ –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏–µ –ø–æ—Ä—Ü–∏–∏ –µ–ª–µ–º–µ–Ω—Ç–æ–≤ –∏–∑ –±–∏–±–ª–∏–æ—Ç–µ–∫–∏. –ü–æ –¥–µ—Ñ–æ–ª—Ç—É - –¥–æ 20 —à—Ç.
     * –î–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –Ω–∞–¥–æ –ø–µ—Ä–µ–¥–∞—Ç—å –∞—Ä–≥—É–º–µ–Ω—Ç —Å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ–º
    */
    fetchData(quantity = 20) {
        const storageData = this.getData(this.activeLibrary);

        if (!storageData || storageData.total_results===0) {
            return null;
        }

        const outputData = {
            page: this.page,
            results: [...storageData.library.slice(((this.page - 1) * 20), (this.page - 1) * 20 + quantity)],
            total_pages: storageData.total_pages,
            total_results: storageData.total_results
        };
        
        return JSON.stringify(outputData);
    }



    /*–º–µ—Ç–æ–¥ –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –Ω–∞–ª–∏—á–∏—è —Ñ–∏–ª—å–º–∞ –≤ –±–∏–±–ª–∏–æ—Ç–µ–∫–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è*/
    availabilityChecking(filmId) {
        const library = {
            0: "watched",
            1: "queue"
        }

        const keys = Object.keys(library);
        let isAvailable = false;
        let sourceLibrary = null;
        
        for (const key of keys) {
            const storageData = this.getData(library[key]);

            if (!storageData || storageData.total_results===0) {
                continue;
            }

            storageData.library.find(el => {
                if (el.id !== filmId) return false;
                isAvailable = true;
                sourceLibrary = library[key];
                return true;
            });

            if (isAvailable) {
                return { isAvailable, sourceLibrary };
            }
        };

        return { isAvailable, sourceLibrary };
    }    


    
    /*–º–µ—Ç–æ–¥ –¥–ª—è —É–≤–µ–ª–∏—á–µ–Ω–∏—è –∑–Ω–∞—á–µ–Ω–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã –¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏*/
    incrementPage() {
        this.page += 1;
    }

    /*–º–µ—Ç–æ–¥ –¥–ª—è —Å–±—Ä–æ—Å–∞ —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏*/
    resetPage() {
        this.page = 1;
    }

    /*–º–µ—Ç–æ–¥ –¥–ª—è —Ñ–∏–∫—Å–∞—Ü–∏–∏ –Ω–∞–∑–≤–∞–Ω–∏—è –∞–∫—Ç–∏–≤–Ω–æ–π –±–∏–±–ª–∏–æ—Ç–µ–∫–∏*/
    setActiveLibrary(libraryName) {
        this.activeLibrary = libraryName;
    }

    /*–º–µ—Ç–æ–¥ –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –æ–∫–æ–Ω—á–∞–Ω–∏—è –æ–±—ä–µ–∫—Ç–æ–≤ –≤ –±–∏–±–ª–∏–æ—Ç–µ–∫–µ*/
    setEndStatus() {
        this.isEndStatus = true;
    }

    /*–º–µ—Ç–æ–¥ –¥–ª—è —Å–±—Ä–æ—Å–∞ –∑–Ω–∞—á–µ–Ω–∏—è —É–∫–∞–∑—ã–≤–∞—é—â–µ–≥–æ –Ω–∞ –æ–∫–æ–Ω—á–∞–Ω–∏–µ –æ–±—ä–µ–∫—Ç–æ–≤ –≤ –±–∏–±–ª–∏–æ—Ç–µ–∫–µ*/
    resetEndStatus() {
        this.isEndStatus = false;
    }
}