import axios from 'axios';
import { saveGenres, saveFilms, savePopularFilms } from '../components/session-storage';

export default class MovieApi {
  constructor() {
    this.API_KEY = '16793a08fc468099c942dee45d510578';
    this.BASE_URL = 'https://api.themoviedb.org/3/';
    this.genres = {};
    this.moviesObj = {};
    this.currentPage = 1;
    this.rows = 20;
    this.query = '';

    // this.VIDEO_BASE_URL = 'https://api.themoviedb.org/3/movie/';
    // this.IMAGE_BASE_URL = 'https://image.tmdb.org/t/p/w500';

    //–Ω—É–∂–Ω–æ –ø–æ—Å—Ç–∞–≤–∏—Ç—å –¥–µ—Ñ–æ–ª—Ç–Ω—ã–µ –∫–∞—Ä—Ç–∏–Ω–∫–∏, –µ—Å–ª–∏ –Ω–µ—Ç –ø–æ—Å—Ç–µ—Ä–∞
    this.DEFAULT_POSTER = '../images/default-img.png';
    // this.DEFAULT_POSTER = '';   ======= –ø–æ—Å—Ç–µ—Ä –¥–ª—è –æ–∫–Ω–∞ —Å –¥–µ—Ç–∞–ª—è–º–∏
  }

  //–ø–æ–ø—É–ª—è—Ä–Ω—ã–µ —Ñ–∏–ª—å–º—ã –Ω–∞ —Å–µ–≥–æ–¥–Ω—è
  async getTrendingMovies() {
    const response = await axios.get(`${this.BASE_URL}trending/movie/day?api_key=${this.API_KEY}`);
    const movies = response.data.results;
    this.moviesObj = response.data;
    movies.map(el => {
      const posterImageExist = el.poster_path;
      if (!posterImageExist) {
        el.poster_path = MyApi.DEFAULT_POSTER;
      }
    });
    savePopularFilms(this.moviesObj);
    saveFilms(this.moviesObj.results);
    return movies;
  }

  //–ø–æ–∏—Å–∫ —Ñ–∏–ª—å–º–∞
  async searchMovies(query) {
    console.log(query);
    const response = await axios.get(
      `${this.BASE_URL}search/movie?query=${query}&api_key=${this.API_KEY}&language=en-US&page=${this.currentPage}&include_adult=false`,
    );
    const movies = response.data.results;
    this.moviesObj = response.data;
    // console.log(movies);
    movies.map(el => {
      const posterImageExist = el.poster_path;
      if (!posterImageExist) {
        el.poster_path = MyApi.DEFAULT_POSTER;
      }
    });
    saveFilms(movies);
    return movies;
  }

  //–¥–µ—Ç–∞–ª–∏ —Ñ–∏–ª—å–º–∞
  async movieDetails(movie_id) {
    const response = await axios.get(
      `${this.BASE_URL}movie/${movie_id}?api_key=${this.API_KEY}&language=en-US`,
    );
    const data = response.data;
    // console.log(data);
    return data;
  }

  //–∂–∞–Ω—Ä—ã
  async genresList() {
    const response = await axios.get(`
      ${this.BASE_URL}genre/movie/list?api_key=${this.API_KEY}&language=en-US`);
    const data = response.data;

    //   console.log(data);
    this.genres = data;

    saveGenres(data);
    return this.genres;
  }

  resetPage() {
    return (this.currentPage = 1);
  }
}

